import formatProfilePicture from 'workers/processExtractedData/utils/formatProfilePicture';
import {
	formatRevenueRange,
	formatStaffCountRange,
	getNormalizedEmployeeCount,
} from 'workers/processExtractedData/utils/organization';
import {
	formatCompanyLocationObject,
	getCompanyLocationStringFromObject,
} from 'workers/processExtractedData/utils/contactInfo';
import { cleanEmptyNullData } from 'utils/data';
import hasResults from 'utils/hasResults';
import isEmpty from 'utils/coreDupes/isEmpty';
import isObject from 'utils/coreDupes/isObject';

const STANDARD_COMPANY_PROFILE_URL_BASE = 'https://www.linkedin.com/company/';
const PREMIUM_COMPANY_PROFILE_URL = (linkedInId) =>
	`https://www.linkedin.com/sales/accounts/insights?companyId=${linkedInId}&trk=li_comp_page`;

const INVALID_WEBSITES = ['linkedin.com', 'licdn.com'];
function isValidCompanyWebsite(website) {
	return (
		website &&
		INVALID_WEBSITES.every((invalidUrl) => !website.includes(invalidUrl))
	);
}

const createSeamlessCompany = (data) => {
	const {
		// LinkedIn Identifiers
		linkedInId,
		linkedInProfileIdentifier,
		linkedInUrn,

		// LinkedIn Profile URLs
		linkedInProfileUrl, // Currently NOT part of the Seamless API company model
		linkedInSalesNavUrl,

		// Entity info
		name,
		logo,
		description,

		// Company info
		annualRevenue,
		foundedOn,
		industries,
		specialities,
		staffCount,
		staffCountRange: staffRange,
		type,

		// Company contact info
		companyPageUrl,
		companyUrl,
		website,
		confirmedLocations,
		headquarter,
		headquarters,
		locationFullString,

		// Social
		linkedInFollowerCount,

		// Relations
		affiliatedCompanies,
	} = data;

	const addresses = locationFullString ? [locationFullString] : [];

	let location = locationFullString;
	if (isObject(headquarter) || isObject(headquarters)) {
		const _headquarter = formatCompanyLocationObject(
			headquarter || headquarters
		);
		if (!isEmpty(_headquarter)) {
			addresses.push(_headquarter);
			if (!location) {
				location = getCompanyLocationStringFromObject(_headquarter);
			}
		}
	}

	if (hasResults(confirmedLocations)) {
		for (const confirmedLocation of confirmedLocations) {
			const formattedLocation =
				formatCompanyLocationObject(confirmedLocation);
			if (formattedLocation) {
				addresses.push(formattedLocation);
			}
		}
	}

	const staffCountRange = staffRange && formatStaffCountRange(staffRange);
	const employeeCountNmlzd =
		(staffCount || staffCountRange) &&
		getNormalizedEmployeeCount({
			count: staffCount,
			range: staffCountRange,
		});

	const parsedAnnualRevenue =
		annualRevenue && Number.parseFloat(annualRevenue).toFixed(0);

	const companyWebsite = website || companyPageUrl || companyUrl;

	let company = {
		linkedInId,
		linkedInProfileIdentifier,
		linkedInUrn,
		linkedInProfileUrl:
			linkedInProfileUrl ||
			(linkedInProfileIdentifier &&
				`${STANDARD_COMPANY_PROFILE_URL_BASE}${linkedInProfileIdentifier}`),
		linkedInSalesNavUrl:
			linkedInSalesNavUrl ||
			(linkedInId && PREMIUM_COMPANY_PROFILE_URL(linkedInId)),
		name,
		description,
		companyLogoUrl: logo && formatProfilePicture(logo),
		annualRevenue: parsedAnnualRevenue,
		revenueRevenue:
			parsedAnnualRevenue && formatRevenueRange(parsedAnnualRevenue),
		foundedOn,
		industries,
		specialities,
		staffCount,
		staffCountRange,
		employeeCountNmlzd,
		type,
		addresses: hasResults(addresses) ? addresses : null,
		location,
		website: isValidCompanyWebsite(companyWebsite) ? companyWebsite : null,
		linkedInFollowerCount: linkedInFollowerCount && ~~linkedInFollowerCount,
		affiliatedCompanies,
		itemType: 'company',
	};

	company = cleanEmptyNullData(company);
	return company;
};

export default createSeamlessCompany;
