import { getMax } from 'utils/number';

const REVENUE_RANGES = {
	'$0-$100K': [0, 100000],
	'$100K-$1M': [100001, 1000000],
	'$1M-$5M': [1000001, 5000000],
	'$5M-$20M': [5000001, 20000000],
	'$20M-$50M': [20000001, 50000000],
	'$50M-$100M': [50000001, 100000000],
	'$100M-$500M': [100000001, 500000000],
	'$500M-$1B': [500000001, 1000000000],
	'$1B+': [1000000001, 200000000000000],
};

/**
 * Function that takes an annual revenue and produces a string range
 * @param {string} - annualRevenue
 * @returns {string} - The annual revenue converted to a set range
 */
export function formatRevenueRange(annualRevenue) {
	const val = parseInt(annualRevenue, 10);
	return Object.keys(REVENUE_RANGES).filter((k) => {
		const range = REVENUE_RANGES[k];
		return val >= range[0] && val <= range[1];
	})[0];
}

export function formatStaffCountRange(staffRange) {
	let _staffRange = staffRange;
	if (_staffRange === 'myself only') {
		_staffRange = '0 - 1';
	} else if (_staffRange?.start != null) {
		if (_staffRange.end) {
			_staffRange = `${_staffRange.start} - ${_staffRange.end}`;
		} else {
			_staffRange = `${_staffRange.start}`;
		}
	}

	return _staffRange;
}

/**
 *
 * @param count
 * @param range
 * @returns {string}
 */
export function getNormalizedEmployeeCount({ count, range }) {
	let _count = count;
	if (!_count && (!range || (typeof range === 'string' && !range.length))) {
		// in case one is zero return that
		return typeof _count !== 'undefined' ? _count : null;
	}

	_count = ~~(
		(typeof _count === 'string' &&
			_count.length &&
			~~_count.replace(/\D/g, '')) ||
		_count
	);
	if (!range || (typeof range === 'string' && !range.length)) {
		return _count;
	}

	// can have either a `-` or `to` as the range separator
	let rangeLower;
	if (range === 'Less than 25') {
		rangeLower = 1;
	} else {
		rangeLower =
			(range && range.start) ||
			(typeof range === 'string' &&
				range.length &&
				range
					.replace(/\s/g, '')
					.split('-')[0]
					.split('to')[0]
					.replace(/\D/g, ''));
	}

	return getMax([~~rangeLower || 1, _count]);
}
