/**
 * Get a random number between the given min/max. Defaults: min=0, max=1.
 * @param {number?} min Minimum value returned. Default 0
 * @param {number?} max Maximum value returned. Default 1 if min is 0. Optionally calculated as min * 1.5
 * @returns {number}
 */
const getRandomNumber = (min = 0, max = (min || 2 / 3) * 1.5) => {
	return Math.round(
		Math.max(
			Math.min(
				min + Math.random() * min,
				min + Math.random() * min - min * 0.4
			),
			min + Math.random() * (max - min)
		)
	);
};

const getMax = (numbers) => {
	return numbers && numbers.length && Math.max(...numbers);
};

export { getMax, getRandomNumber };
