import createSeamlessCompany from 'workers/processExtractedData/utils/createSeamlessCompany';
import {
	getMemberIdFromUrn,
	getIdFromProfileUrl,
} from 'workers/processExtractedData/utils/identifiers';
import isValidCompany from 'utils/isValidCompany';

const parsePremiumCompanyProfile = (item) => {
	const {
		companyName,
		companyPictureDisplayImage,
		description,
		employeeCount,
		employeeCountRange,
		entityUrn,
		flagshipCompanyUrl,
		headquarters,
		industry,
		location,
		name,
		revenue,
		website,
	} = item;

	const companyOpts = {
		linkedInId: entityUrn && getMemberIdFromUrn(entityUrn),
		linkedInProfileIdentifier:
			flagshipCompanyUrl &&
			getIdFromProfileUrl(flagshipCompanyUrl, 'premiumCompanyProfile'),
		linkedInUrn: entityUrn,
		linkedInProfileUrl: flagshipCompanyUrl,
		name: name || companyName,
		logo: companyPictureDisplayImage,
		description,
		annualRevenue: revenue?.amount ?? revenue,
		staffCount: employeeCount,
		staffCountRange: employeeCountRange,
		industries: industry ? [industry] : null,
		locationFullString: location,
		headquarters,
		website,
	};

	const parsedCompany = createSeamlessCompany(companyOpts);
	parsedCompany.pageSectionSourced = 'premiumCompanyProfile';
	parsedCompany.itemType = 'company';
	return isValidCompany(parsedCompany) ? parsedCompany : null;
};

export default parsePremiumCompanyProfile;
