import { expose } from 'comlink';
import parsePremiumCompanyProfile from 'workers/processExtractedData/processPremiumCompanyProfileData/parsePremiumCompanyProfile';
import parsePremiumCompanyAccountMapEntries from 'workers/processExtractedData/processPremiumCompanyProfileData/parsePremiumCompanyAccountMapEntries';
import parsePremiumCompanyPeopleAlsoViewed from 'workers/processExtractedData/processPremiumCompanyProfileData/parsePremiumCompanyPeopleAlsoViewed';
import parsePremiumCompanyRecommendedLeads from 'workers/processExtractedData/processPremiumCompanyProfileData/parsePremiumCompanyRecommendedLeads';
import parsePremiumCompanySavedLeads from 'workers/processExtractedData/processPremiumCompanyProfileData/parsePremiumCompanySavedLeads';
import isEmpty from 'utils/coreDupes/isEmpty';

function hasPremiumCompanyProfileData(data) {
	const type = data?.$type?.toLowerCase();
	return (
		(type?.includes('company') &&
			// Filter out org photo data
			!type?.includes('organizationphoto') &&
			!type?.includes('targetedcontent')) ||
		data?.entityUrn?.toLowerCase().includes('company')
	);
}

const SAVED_LEADS_PROP = 'savedLeads';
const hasSavedLeads = (data) => Boolean(data[SAVED_LEADS_PROP]?.length);

const RECOMMENDED_LEADS_PROP = 'recommendedLeads';
const hasRecommendedLeads = (data) =>
	Boolean(data[RECOMMENDED_LEADS_PROP]?.length);

const ACCOUNT_MAP_ENTRIES_PROP = 'accountMapEntries';
const hasAccountMapEntries = (data) =>
	Boolean(data[ACCOUNT_MAP_ENTRIES_PROP]?.length);

function hasContactData(data) {
	return (
		hasSavedLeads(data) ||
		hasRecommendedLeads(data) ||
		hasAccountMapEntries(data)
	);
}

const PREMIUM_COMPANY_ALSO_VIEWED_PROP = 'companiesAlsoViewedResolutionResults';
const hasCompaniesPeopleAlsoViewed = (data) =>
	Object.prototype.hasOwnProperty.call(
		data,
		PREMIUM_COMPANY_ALSO_VIEWED_PROP
	);

const processPremiumCompanyProfileData = async (pageData) => {
	let profileUrlOverride;
	let parsedItems = [];

	if (hasContactData(pageData)) {
		let contactParsers = [];
		if (hasSavedLeads(pageData)) {
			const savedLeadsParsers = pageData[SAVED_LEADS_PROP].map((item) =>
				parsePremiumCompanySavedLeads(item)
			);
			if (contactParsers?.length) {
				contactParsers = [...contactParsers, ...savedLeadsParsers];
			}
		}
		if (hasRecommendedLeads(pageData)) {
			const recommendedLeadsParsers = pageData[
				RECOMMENDED_LEADS_PROP
			].map((item) => parsePremiumCompanyRecommendedLeads(item));
			if (contactParsers?.length) {
				contactParsers = [
					...contactParsers,
					...recommendedLeadsParsers,
				];
			}
		}
		if (hasAccountMapEntries(pageData)) {
			const accountMapEntriesParsers = pageData[
				ACCOUNT_MAP_ENTRIES_PROP
			].map((item) => parsePremiumCompanyAccountMapEntries(item));
			if (contactParsers?.length) {
				contactParsers = [
					...contactParsers,
					...accountMapEntriesParsers,
				];
			}
		}

		if (contactParsers?.length) {
			parsedItems = await Promise.all(contactParsers);
		}
	} else if (hasCompaniesPeopleAlsoViewed(pageData)) {
		const companyParsers = pageData[
			PREMIUM_COMPANY_ALSO_VIEWED_PROP
		]?.map?.((item) => parsePremiumCompanyPeopleAlsoViewed(item));
		if (companyParsers?.length) {
			parsedItems = await Promise.all(companyParsers);
		}
	} else if (hasPremiumCompanyProfileData(pageData)) {
		const premiumCompanyProfileData = parsePremiumCompanyProfile(pageData);
		if (!isEmpty(premiumCompanyProfileData)) {
			profileUrlOverride =
				premiumCompanyProfileData?.linkedInSalesNavUrl || null;
			parsedItems = [premiumCompanyProfileData];
		}
	}

	return {
		profileUrlOverride,
		parsedItems,
	};
};

expose(processPremiumCompanyProfileData, self);
